import React from "react";
import Typewriter from "typewriter-effect";
import "./Typewriter.css"
function Writer() {
  return (
    <div className='typewriter'>
    <Typewriter options={{loop:true}}
      onInit={(typewriter) => {
        typewriter.typeString("I am a developer.").pauseFor(2500).deleteAll();
        typewriter.typeString("I am a tech enthusiast.").pauseFor(2500).deleteAll();
        typewriter.typeString("I am a programmer.").pauseFor(2500).deleteAll();
        typewriter.typeString("I am an innovator.").pauseFor(2500).deleteAll();
        typewriter.typeString("I am an educator.").pauseFor(2500).deleteAll();
        typewriter.typeString("I am an entrepreneur.").pauseFor(2500).deleteAll()
        .start();
      }}>
     </Typewriter>
    </div>
  );
}

export default Writer;
